.section-10 {
  .consultation {
    box-shadow: 0 40px 80px rgb(0 0 0 / 4%);
    border-radius: 16px;
    background-color: #ffffff;
    position: relative;

    &-inner {
      position: relative;
      max-width: 70%;
      padding-top: 61px;
      padding-left: 62px;
      padding-bottom: 40px;
      z-index: 5;

      .consultation__title {
        text-align: left;
        color: #0f1013;
        font-size: 38px;
        font-weight: 700;
        line-height: 46px;
        margin-bottom: 13px;
      }

      .consultation__subtitle {
        font-size: 20px;
        font-weight: 400;
        line-height: 30px;
        margin-bottom: 17px;
      }

      .consultation-list {
        list-style: none;
        display: flex;
        justify-content: space-between;
        padding-left: 0;
        padding-right: 85px;
        margin-bottom: 35px;

        li {
          display: flex;
          align-items: flex-start;
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;

          img {
            margin-top: 5px;
            margin-right: 13px;
          }
        }
      }

      input {
        width: 368px;
        height: 102px;
        font-size: 18px;
        font-weight: 400;
        border-radius: 51px;
      }

      .main-btn {
        font-size: 16px;
        line-height: 21px;
        font-weight: 700;
        width: 380px;
        height: 96px;
        position: relative;
        z-index: 1;
        border-radius: 48px;
      }


    }

    .consultation__image {
      position: absolute;
      top: -84px;
      right: -594px;
      z-index: 1;
    }
  }
}